import React, { useContext, useEffect } from "react";
import Moosix from "../functions/mossix-api.js"
import {uidContext} from "../../context/uid"
import { useState } from "react"
import {MoosaxAuth} from "../functions/moosax-auth.js"
const app = new Moosix()
function Like(props){
    const auth = new MoosaxAuth()
    const [cUID, setCUID] = useState()
    const uID = useContext(uidContext)
    let music = props.music
    var title = props.name //sets tractk title
    let userName = props.userName //sets userName
    const [likeNumber, setLikeNumber] = useState(music.stats.likes)
    const [like, setLike] = useState()
    const hasLiked = music.hasLiked
        useEffect(()=>{
           
            //goes to firebase and check if the value is true or undefined
            if(hasLiked){
                setLike(true)
            }else{
                setLike(false)
            }
        }, [userName])
        
        function sendLike(){
            if(uID){
                app.sendLike(cUID, uID, music.info.songID, music)
                if(like){
                    setLikeNumber(likeNumber - 1)
                    setLike(false)
                }else{
                    setLike(true)
                    setLikeNumber(likeNumber+ 1)
                }
            }
           
        }
    return(
        <div className = "like" id = {props.id}>
            <i className="material-icons" onClick = {sendLike} >{like ? <p>favorite</p> : <p>favorite_border</p> }</i>
           <span>{likeNumber}</span>
        </div>
    )
}
export default Like